* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: Candara, Calibri, Segoe, Segoe UI, Optima, Arial, sans-serif;
}
li {
  list-style: none;
}

/* nav style  */
.nav {
  width: 100%;
  height: 100px;
  position: sticky;
  top: 0;
  display: flex;
  border-bottom: 2px solid #044a7336;
  justify-content: space-between;
  padding: 40px 100px 40px 40px;
  align-items: center;
  background-color: #fff;
}
.logo {
  color: #044a73;
}

.nav-items ul {
  display: flex;
}

.nav-item {
  margin-right: 40px;
  border-bottom: 3px solid #fff;
  transition: 0.4s ease;
  cursor: pointer;
  color: #044a73;
  font-size: 20px;
}
.nav-item:hover {
  border-bottom: 3px solid #5c8aff;
  color: #5c8aff;
}
.burger {
  display: none;
  width: 30px;
}
.burger > div {
  padding: 3px;
  background-color: #044a73;
  border-radius: 15px;
  margin-bottom: 4px;
}
.burger > div:last-child {
  margin-bottom: 0px;
}
/* Socail app */
.social {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 100px;
  background-color: #044a73;
}
.burger-social {
  height: 100px;
  padding: 40px 20px;
}
.burger-social > div {
  width: 50px;
  height: 4px;
  color: white;
  background-color: white;
  margin-bottom: 15px;
  float: right;
}
.social-item {
  display: flex;
  justify-content: space-around;
  height: 60px;
  color: white;
  /* padding: 40px 20px; */
}

/* Home */
.home {
  padding: 40px;
  display: flex;
  justify-content: space-between;
}
.header-title {
  display: flex;
  align-items: center;
  color: #5c8aff;
}
.squre {
  width: 45px;
  height: 40px;
  background-color: #044a73;
  border-radius: 10px;
  margin-right: 15px;
}
.main-title {
  font-size: 1.2rem;
}
.name {
  font-size: 1.9rem;
  padding: 40px 0 0 30px;
  word-spacing: 5px;
}
.homeImg img {
  width: 750px;
}

/* about page  */
.about-container {
  display: flex;
  align-items: center;
  padding: 40px 120px 40px 0px;
}
.about-box {
  width: 60%;
}

.introduction {
  display: inline-block;
  width: 100%;
  margin-top: 20px;
}
.introduction h1 {
  font-size: 60px;
  margin-bottom: 10px;
}
.introduction p {
  font-size: 20px;
  font-family: "Bahnschrift", Times, serif;
  color: rgba(0, 0, 0, 0.548);
  width: 90%;
}
.about-me {
  margin-top: 30px;
  color: #044a73;
}
.about-me h1 {
  margin-bottom: 18px;
}
.about-item {
  display: flex;
  color: black;
  margin-bottom: 15px;
}
.about-item > div {
  width: 20px;
  width: 23px;
  background-color: #044a73;
  border-radius: 8px;
  margin-right: 20px;
}
.about-img img {
  width: 600px;
}
.details{
  width: 170px;
}

@media screen and (max-width: 1366px) {
  .homeImg img {
    width: 650px;
  }
  .about-img img {
    width: 550px;
  }
  .name {
    font-size: 1.7rem;
  }
}
@media screen and (max-width: 1133px) {
  .about-img img {
    width: 450px;
  }
}
@media screen and (max-width: 1024px) {
  .nav-item {
    margin-right: 30px;
  }
}

@media screen and (max-width: 968px) {
  .homeImg img {
    width: 550px;
  }
  .about-img img {
    width: 400px;
  }
  .name {
    font-size: 1.6rem;
  }
  .social {
    display: none;
  }
  .nav {
    padding: 40px 20px 40px 40px;
  }
  .burger {
    display: block;
  }
  .resNav6 {
    display: none;
  }
}
@media screen and (max-width: 825px) {
  .homeImg img {
    width: 450px;
  }
  .about-img img {
    display: none;
  }
  .name {
    font-size: 1.5rem;
  }
  .resNav5 {
    display: none;
  }
  .about-box {
    width: 100% !important;
  }
  .about-container {
    padding: 40px;
  }
}

@media screen and (max-width: 720px) {
  .homeImg img {
    width: 400px;
  }
  .resNav4 {
    display: none;
  }
}

@media screen and (max-width: 670px) {
  .home {
    display: block;
  }
  .name {
    text-align: center;
    padding: 20px 0 20px 0;
  }
  .homeImg {
    text-align: center;
  }

  .nav-item {
    display: none;
  }
  .burger {
    display: block;
  }
  .details{
    width: 140px;
  }
}

@media screen and (max-width: 468px) {
  .home {
    padding: 20px;
  }
  .homeImg img {
    width: 300px;
  }
  .nav {
    padding: 30px 0px 30px 10px;
  }
  .squre {
    width: 33px;
    height: 30px;
  }
  .main-title {
    font-size: 1rem;
  }
  .about-container {
    padding: 20px;
  }
  .details{
    width: 80px;
  }
  .about-item{
    font-size: small;
  }
}
